body {
  font-family: -apple-system, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.5em;
  text-align: left;
}

ul {
  list-style: decimal;
}

ol {
  font-family: monospace;
}

h2 {
  margin: 0em, .5em;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
  padding: 1em;
};

.flex-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
}

.header {
  text-align: center;
  align-items: flex-end;
}

.stats {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
}

.scaffold {
  text-align: center;
}

#scaffoldImg,
#gameModalHeader {
  transition: all .07s ease;
}

.missing {
  transform: scale(1.2);
}

.wordWork,
.wordChallenge {
  justify-content: center;
}

.wordChallenge > * {
  flex: 1 0 auto;
}

.wordWork {
  text-align: left;
  margin-left: 50px;
  flex: 0 0 auto;
}

#wordField {
  white-space: nowrap;
}

.definitionField {
  text-align: left;
}

.field {
  font-family: monospace;
}

.buttons {
  text-align: center;
}

button {
  font-family: -apple-system, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-style: bold;
  padding: 5px 10px;
  border-radius: 2px;
}

/* borrowed from https://sabe.io/tutorials/how-to-create-modal-popup-box */
.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* opacity: 0;
  visibility: hidden; */
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
  text-align: center;
}
.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 1rem 1.5rem;
  width: 30rem;
  border-radius: 0.5rem;
}
.score-modal-content {
  width: 15em;
}
.close-button {
  float: right;
  width: 1.2rem;
  line-height: 1.2rem;
  text-align: center;
  cursor: pointer;
  border-radius: 0.25rem;
  background-color: lightgray;
}
.close-button:hover {
  background-color: darkgray;
}
.show-modal {
  opacity: 1;
  visibility: visible;
  transform: scale(1.0);
  transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
}
.modal-header,
#scoreList {
  font-family: 'Courier New', Courier, monospace;
}
.modal-list {
  text-align: center;
  list-style: none;
  padding: 0;
}
.modal-list-item {
  padding-bottom: 1em;
  font-weight: bold;
}
.modal-list-item li {
  list-style: none;
};

.center-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
};
